import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatus from "./ProfileStatus"
import ProfileStatusWithHooks from "./ProfileStatusWhithHooks";
import userPhoto from "../../../assets/images/user.png";


const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto}) => {
    if (!profile) {
        return <Preloader />
    }

   const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
           savePhoto(e.target.files[0])
        }
   }

    return (
        <div>
            <div className={s.descriptionBlock}>
                <img src={profile.photos.large || userPhoto} className={s.mainPhoto} />
                {isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}

                <div><ProfileStatusWithHooks status={status}
                updateStatus={updateStatus}/></div>
                <div>
                    <b>Name: {profile.name} </b>
                    </div>
                <div>
                    <b>About me: </b> {profile.aboutMe}
                </div>
                <div>
                    <b>Contacts: </b>
                    facebook: {profile.contacts.facebook}<br/>
                    website: {profile.contacts.website}<br/>
                    vk: {profile.contacts.vk}<br/>
                    instagram: {profile.contacts.instagram}<br/>
                    twitter: {profile.contacts.twitter}<br/>
                    youtube: {profile.contacts.youtube}<br/>
                    github: {profile.contacts.github}<br/>
                </div>
            </div>
        </div>
    )
}

